@import '../../variables/variables.scss';

.featured-works {
	padding: 30px var(--safe-side-paddings);
	width: 100%;
	background-color: var(--bg);
}

.featured-works__wrap {
	display: grid;
	grid-template-areas:
		'title link'
		'worksList worksList';
	grid-template-columns: 1fr 1fr;
	grid-template-rows: min-content 1fr;
	gap: 20px;
}

.featured-works__title {
	font-family: var(--basic-font);
	font-weight: var(--s-title-weight);
	font-size: var(--s-title-size);
	color: var(--primary-text);
	grid-area: title;
}

.featured-works__all-works-link {
	align-self: center;
	justify-self: flex-end;
	grid-area: link;
	font-family: var(--basic-font);
	font-weight: var(--s-text-weight);
	font-size: var(--s-text-size);
	text-decoration: none;
	color: var(--link-text);

	&:hover {
		text-decoration: underline;
	}
}

.featured-works__list {
	grid-area: worksList;
}
